import pageLoader from "./common-components/pageLoader";
import landscapeOff from "./common-components/landscapeOff";
import inView from './components/in-view';
import animateObserver from "./common-components/animateObserver";
import selectTo from './common-components/selectTo';
import mobileMenu from './common-components/mobile-menu';

landscapeOff();
animateObserver();
mobileMenu();

function Home() {
    // Helper method to add class
    this.addClass = (element, className) => {
        // Getting existing classes
        const classNames = element.getAttribute('class') || '';
        // Setting new attribute without active
        element.setAttribute('class', classNames + ' ' + className);
    };

    // Helper method to remove class
    this.removeClass = (element, className) => {
        // Getting existing classes
        const classNames = element.getAttribute('class') || '';
        // Replacing class to be removed
        const _classNames = classNames.replace(className, '').trim();
        // Setting new attribute without active
        element.setAttribute('class', _classNames);
    };

    // Helper to remove active class form elements
    this.removeActiveClass = (elements = []) => {
        // Looping each element
        this.each(elements, (element) => {
            // Getting existing classes
            const classNames = element.getAttribute('class') || '';
            // Setting new attribute without active
            element.setAttribute('class', classNames.replace('active', '').trim());
        });
    };

    // Helper method for loop
    this.each = (args = [], callback) => {
        for (let i = 0; i < args.length; i++) {
            callback(args[i]);
        }
    };

    // Initialize intel
    this.initIntel = () => {
        const input = document.querySelector("#phone");
        window.intlTelInput(input, {
            initialCountry: "ae",
            countrySearch: false,
            separateDialCode: true,
            loadUtils: () => import("./components/intel-utils.min"),
        });
    };

    // Feature Tabs
    this.tabs = () => {
        // Getting tabs actions
        const wrappers = document.getElementsByClassName('tabWrapper');
        // Looping through each wrapper
        this.each(wrappers, (wrapper) => {
            // Getting tabs actions
            const actions = wrapper.getElementsByClassName('feature-tab-actions');
            // Looping through each tab actions
            this.each(actions, (actions) => {
                // Getting anchors
                const anchors = actions.getElementsByTagName('a');
                // Looping through each anchor
                this.each(anchors, (anchor) => {
                    // Add event listener
                    anchor.addEventListener('click', (e) => {
                        // Prevent default action
                        e.preventDefault();
                        // Removing active class
                        this.removeActiveClass(anchors);
                        // Adding active class
                        this.addClass(anchor, 'active');
                        // Getting name for the tab
                        const tab = document.getElementById(anchor.getAttribute('data-tab'));
                        // Removing active class
                        this.removeActiveClass(wrapper.getElementsByClassName('tabContent'));
                        // Showing tab
                        this.addClass(tab, 'active');
                    });
                });
            });
        });
    }

    // Featured Sliders
    this.carousel = (identity, options) => {
        new Swiper(identity, options)
    }

    // Horizontal info tabs
    this.horizontalTabs = () => {
        // Core values details
        const backgroundElement = document.getElementById('itemWrapperBackground');
        // Actions triggers
        const triggers = document.getElementsByClassName('cardItem');
        // Looping through each item
        this.each(triggers, (trigger) => {
            trigger.addEventListener('pointerenter', () => {
                // Removing active class
                this.removeActiveClass(triggers);
                // Getting background image
                const attr = trigger.getAttribute('data-bg');
                // Adding active class
                this.addClass(trigger, 'active');
                // Setting background image
                backgroundElement.setAttribute('style', `background-image: url("${attr}")`);
            });
        });
    }

    // Method to add sticky item on scroll
    this.addStickyItems = () => {
        // Getting header
        const header = document.getElementById('header');
        // Adding scroll event listener
        window.addEventListener("scroll", () => {
            // Checking scrolled height
            window.scrollY > 1 ?
                // Adding sticky class to header
                header.setAttribute('class', 'sticky') :
                // Adding sticky class to header
                this.removeClass(header, 'sticky');
        });
    };

    this.heroBanner = () => {
        setTimeout(() => {
            // Getting hero banner
            const hero = document.getElementById('heroBanner');
            hero.setAttribute('class', 'heroBanner animated');
        }, 5000);
    };

    // Initialize animations
    this.initAnimations = () => {
        inView.threshold(0.7);

        // About section observer
        const aboutElement = document.getElementById('aboutImage');
        inView('#aboutImage').on('enter', () => {
            aboutElement.setAttribute('class', 'aboutBg animateIn');
        });

        const revelImageElement = document.getElementById('revel-image');
        inView('#revel-image').on('enter', () => {
            revelImageElement.setAttribute('class', 'aboutImg-1 animated');
        });

        // Feature section observer
        let isAnimated = false;
        const featuredElement = document.getElementById('featuredAnimation1');
        const featuredElementSlider = document.getElementById('featuredAnimationSlider');
        inView('#featuredAnimation1').on('enter', () => {
            if (!isAnimated) {
                featuredElement.setAttribute('class', 'featuredAnimation animated');
                setTimeout(() => {
                    featuredElement.setAttribute('class', 'featuredAnimation animated animate-cards');
                    isAnimated = true;
                }, 1500);

                setTimeout(() => {
                    featuredElement.setAttribute('class', 'featuredAnimation animated animate-cards fadeOut');
                    featuredElementSlider.setAttribute('class', 'featuredSectionContent fadeIn')
                }, 2500);
            }
        });
    }
}

// Creating home instance
const home = new Home();
// Home tabs
home.tabs();
// Hero slider
home.carousel('.heroSlider', {
    spaceBetween: 0,
    effect: "fade",
    autoplay: {
        delay: 5000,
        disableOnInteraction: false,
    },
    pagination: {
        el: ".hero-pagination",
        clickable: true,
    },
});
// Creating feature carousel
home.carousel('.featuredSlider', {
    slidesPerView: 1,
    spaceBetween: 0,
    pagination: {
        el: ".swiper-pagination",
        clickable: true,
    },
    navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
    },
    breakpoints: {
        640: {
            slidesPerView: 1,
            spaceBetween: 0,
        },
        768: {
            slidesPerView: 2,
            spaceBetween: 15,
        },
        1024: {
            slidesPerView: 3,
            spaceBetween: 15,
        },
    },
});
// Creating news carousel
home.carousel('.newsSlider', {
    slidesPerView: 1.1,
    spaceBetween: 20,
    pagination: {
        el: ".news-pagination",
        clickable: true,
    },
    navigation: {
        nextEl: ".news-button-next",
        prevEl: ".news-button-prev",
    },
    breakpoints: {
        640: {
            slidesPerView: 1.1,
            spaceBetween: 15,
        },
        768: {
            slidesPerView: 1.8,
            spaceBetween: 15,
        },
        1024: {
            slidesPerView: "auto",
            spaceBetween: 30,
        },
    },
});
// Initialize intel
home.initIntel();
// Horizontal tabs
home.horizontalTabs();
// Adding scrolled event
home.addStickyItems();
// Initializing G-Sap
home.initAnimations();
// Hero banner
home.heroBanner();

